@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Regular"), local("TTcommons-regular"), url("../fonts/TTCommons-Regular.woff2") format("woff2"), url("../fonts/TTCommons-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Medium"), local("TTcommons-medium"), url("../fonts/TTCommons-Medium.woff2") format("woff2"), url("../fonts/TTCommons-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Bold"), local("TTcommons-bold"), url("../fonts/TTCommons-Bold.woff2") format("woff2"), url("../fonts/TTCommons-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 360px;
  font-family: "TT Commons";
  font-size: 16px;
  line-height: 20px;
  color: #202020;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  overflow: hidden;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1280px;
  margin: 0 auto;
  min-width: 360px;
  padding-top: 20px;
  padding-left: 40px;
  padding-bottom: 20px;
  padding-right: 40px;
  background-color: transparent;
}

.header__logo-image {
  vertical-align: middle;
}

@media (max-width: 1280px) {
  .header__container {
    width: auto;
  }
}
@media (max-width: 870px) {
  .header__container {
    padding-top: 5px;
    padding-left: 20px;
    padding-bottom: 5px;
    padding-right: 20px;
  }
}
.main-nav__list {
  width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 10000;
}

.main-nav__list--footer {
  width: 200px;
  flex-direction: column;
  align-items: start;
  padding-left: 60px;
}

.main-nav__list--footer .main-nav__list-item {
  margin-bottom: 20px;
}

.main-nav__list--footer .main-nav__list-link {
  line-height: 28px;
}

.main-nav__list-link {
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 18px;
  color: #202020;
  line-height: 24px;
  transition: all 0.2s linear;
}
.main-nav__list-link:hover {
  border-bottom-color: #F76501;
}
.main-nav__list-link:active {
  color: #F76501;
  border-bottom-color: #F76501;
}

.main-nav__button {
  display: none;
  width: 50px;
  height: 50px;
  border: none;
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.main-nav__button--closed {
  background-image: url("../img/icon-closed.svg");
}

.main-nav__button--opened {
  background-image: url("../img/icon-opened.svg");
}

@media (max-width: 870px) {
  .main-nav {
    order: 3;
  }
  .main-nav__list {
    background-color: #FFFFFF;
  }
  .main-nav__list--footer {
    background-color: transparent;
  }
  .main-nav > .main-nav__list {
    display: none;
  }
  .main-nav--show > .main-nav__list {
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .main-nav--show .main-nav__list-item {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #eeeeee;
    text-align: center;
  }
  .main-nav--show .main-nav__list-item:first-child {
    border-top: 1px solid #eeeeee;
  }
  .main-nav__button {
    display: block;
  }
}
@media (max-width: 700px) {
  .main-nav__list--footer {
    padding-left: 100px;
  }
}
@media (max-width: 400px) {
  .main-nav__list--footer {
    padding-left: 80px;
  }
}
.phones-list {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 40px;
  margin: 0;
  padding: 0;
  padding-left: 50px;
  list-style: none;
}
.phones-list::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.phones-list__item {
  padding-top: 5px;
}

.phones-list__link {
  font-weight: 500;
  font-size: 22px;
  line-height: 28px;
}

.phones-list__link--footer {
  display: block;
  margin-bottom: 20px;
}

@media (max-width: 550px) {
  .phones-list {
    padding-left: 40px;
  }
  .phones-list::before {
    content: "";
    width: 30px;
    height: 30px;
  }
  .phones-list__link {
    font-size: 18px;
    line-height: 24px;
  }
}
.promo {
  margin-top: -110px;
  background-image: url("../img/icon-bg-promo-right.svg"), url("../img/icon-bg-promo-left.svg");
  background-repeat: no-repeat;
  background-position: top 50px right -100px, top -350px left -200px;
}

.promo__container {
