    margin-left: 130px;
  }
  .promo__subtitle {
    margin-left: 130px;
  }
}
@media (max-width: 1220px) {
  .promo__tours {
    width: auto;
  }
  .promo__title {
    margin-left: 60px;
    font-size: 76px;
    line-height: 86px;
  }
  .promo__subtitle {
    margin-left: 60px;
  }
}
@media (max-width: 1010px) {
  .promo__title {
    margin-left: 0;
  }
  .promo__subtitle {
    margin-left: 0;
  }
}
@media (max-width: 800px) {
  .promo__container {
    background-image: url("../img/bg-promo-girl.png"), url("../img/icon-bg-promo-center.svg"), url("../img/icon-bg-promo-compass.svg");
    background-size: 200px auto;
    background-position: top 250px right, top 100px right, top 450px left 80px;
  }
}
@media (max-width: 700px) {
  .promo__container {
    padding-right: 20px;
    padding-left: 20px;
    background-image: url("../img/bg-promo-girl.png"), url("../img/icon-bg-promo-center.svg"), url("../img/icon-bg-promo-compass.svg");
    background-size: 200px auto;
    background-position: top 180px right, top 100px right, top 450px left 80px;
  }
  .promo__title {
    width: 320px;
    font-size: 40px;
    line-height: 50px;
  }
  .promo__subtitle {
    width: 280px;
  }
}
@media (max-width: 520px) {
  .promo__container {
    background-image: url("../img/bg-promo-girl.png"), url("../img/icon-bg-promo-center.svg"), url("../img/icon-bg-promo-compass.svg");
    background-size: 200px auto;
    background-position: top 300px right -40px, top 100px right, top 450px left 80px;
  }
}
.about__container {
  width: 1280px;
  margin: 0 auto;
  padding-right: 40px;
  padding-bottom: 80px;
  padding-left: 40px;
  text-align: center;
}

.about__title {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #454848;
}

.about__subtitle {
  width: 790px;
  margin: 0 auto;
  padding: 0;
}

@media (max-width: 1280px) {
  .about__container {
    width: auto;
  }
}
@media (max-width: 860px) {
  .about__subtitle {
    width: auto;
  }
}
@media (max-width: 700px) {
  .about__container {
    padding-right: 20px;
    padding-left: 20px;
  }
  .about__title {
    font-size: 36px;
    line-height: 42px;
  }
}
.booking-wrap {
  position: relative;
  padding-top: 80px;
  overflow: hidden;
  z-index: 10;
}

.booking {
  position: relative;
  background-image: url("../img/icon-bg-booking.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  z-index: 10;
}
.booking::before {
  content: "";
  position: absolute;
  top: -65px;
  left: 50%;
  transform: translateX(-40%);
  width: 611px;
  height: 167px;
  background-image: url("../img/airplane.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
}

.booking__container {
  position: relative;
  width: 1280px;
  margin: 0 auto;
  min-height: 990px;
  margin: 0 auto;
  padding-top: 160px;
  padding-right: 40px;
  padding-bottom: 80px;
  padding-left: 40px;
}
.booking__container::before {
  content: "";
  position: absolute;
  top: 300px;
  right: -280px;
  width: 700px;
  height: 419px;
  background-image: url("../img/bg-booking-palm.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 700px auto;
  transform: rotate(30deg);
  z-index: 10;
}
.booking__container::after {
  content: "";
  position: absolute;
  bottom: 50px;
  left: -70px;
  width: 180px;
  height: 126px;
  background-image: url("../img/bg-booking-shell.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 10;
}

.booking__title {
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #454848;
  text-align: center;
}

.booking__subtitle {
  margin: 0 auto;
  margin-bottom: 40px;
  padding: 0;
  width: 792px;
  min-height: 40px;
  color: #025472;
  text-align: center;
}

.booking__image-container {
  position: absolute;
  top: 310px;
  left: 40px;
  width: 784px;
  height: 588px;
  border-radius: 25px;
  overflow: hidden;
  z-index: 1;
}

.booking__image {
  width: 784px;
  height: 588px;
  overflow: hidden;
}

.booking__content {
  position: relative;
  width: 590px;
  min-height: 469px;
  margin-top: 90px;
  margin-left: auto;
  padding: 40px 25px 30px;
  border-radius: 22px;
  box-shadow: -12px 6px 34px 3px rgba(0, 0, 0, 0.13);
  background-color: #FFFFFF;
  z-index: 10;
}

.booking-list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  list-style: none;
}

.booking-list__item {
  position: relative;
}
.booking-list__item::before {
  content: "";
  position: absolute;
  top: 5px;
  right: 10px;
  width: 24px;
  height: 24px;
  background-image: url("../img/icon-arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.booking-list__item--active::before {
  background-image: url("../img/icon-arrow-up.svg");
}

.booking-list__button {
  position: relative;
  display: inline-block;
  margin-bottom: 10px;
  padding-bottom: 5px;
  padding-left: 20px;
  border: none;
  background-color: transparent;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #F76501;
  vertical-align: middle;
}
.booking-list__button::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 2px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: #F76501;
}

.booking-list__text {
  display: none;
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  padding-right: 40px;
}

.booking-list__text--show {
  display: block;
}

.booking__button {
  min-width: 225px;
  min-height: 44px;
  padding: 13px 50px;
  border: none;
  border-radius: 4px;
  background-color: #F76501;
  color: #FFFFFF;
  transition: 0.2s linear;
}
.booking__button:hover {
  background-color: #E06B2A;
}
.booking__button:active {
  color: #F76501;
}

@media (max-width: 1280px) {
  .booking__container {
    width: auto;
  }
}
@media (max-width: 900px) {
  .booking__subtitle {
    width: auto;
  }
}
