  }
  .booking__title {
    font-size: 36px;
    line-height: 42px;
  }
}
@media (max-width: 530px) {
  .booking__container {
    min-height: 400px;
    padding-bottom: 40px;
  }
  .booking__container::after {
    bottom: 20px;
